import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class SignService {


  constructor(private _httpClient: HttpClient) { }

  addUrl = 'http://localhost:9091/employee';

  createUser(employee: any) {
    alert(JSON.stringify(employee))
    return this._httpClient.post<any>(this.addUrl + "/create", employee);
  }

  getallemp() {
    return this._httpClient.get<any>(this.addUrl + "/all");
  }

  delete(id: any) {
    return this._httpClient.delete<any>(this.addUrl + "/delete/" + id);
  }

}
