import { Component } from '@angular/core';
import { FormGroup, FormArray, FormBuilder, Validators } from '@angular/forms';
import { HttpErrorResponse } from '@angular/common/http';
import { SignService } from './services/sign.service';
import { createComponent } from '@angular/compiler/src/core';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'my-app',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  createUserForm: FormGroup;
  newUserAdded: boolean = false;
  createErrorMessage: any;
  arr: FormArray;
  responsecreate: any;
  createenable: boolean = false;
  allemployees: boolean = false;
  deleted: boolean = false;
  allemployeeslist: any;
  workdetails: boolean = false;
  nextForm: FormGroup;
  nextenable: boolean = true;
  constructor(private formBuilder: FormBuilder, private _service: SignService, public _datepipe: DatePipe) { }
  ngOnInit() {

    this.newUserAdded = false;
    this.createenable = false;
    this.allemployees = true;
    this._service.getallemp().subscribe((response) => {
      // alert(JSON.stringify(response))
      this.deleted = false;
      this.allemployeeslist = response
      this.createErrorMessage = null;

    }, (error: HttpErrorResponse) => {
      this.createErrorMessage = error.message;
      if (error instanceof Error) {
      } else {
      }
    })

    this.createUserForm = this.formBuilder.group({
      userName: ['',
        [Validators.required]],
      contact: ['',
        [Validators.required,
        Validators.pattern("^[0-9]*$"),
        Validators.minLength(10), Validators.maxLength(10)]],
      address: ['',
        [Validators.required]],
      education: ['',
        [Validators.required]],
      skills: ['', [Validators.required]],
      workexp: ['',
        [Validators.required,
        Validators.pattern("^[0-9]*$")]],
      arr: this.formBuilder.array([this.createItem()])
    })

  }
  createItem() {
    let startDate = (new Date(), 'yyyy-MM-dd');
    let enddate = this._datepipe.transform(new Date(), 'yyyy-MM-dd');

    return this.formBuilder.group({
      company: ['',
        [Validators.required]],
      designation: ['',
        [Validators.required]],
      companyexp: ['',
        [Validators.required,
        Validators.pattern("^[0-9]*$")]],
      jobdes: ['',
        [Validators.required]],
      startdate: [startDate, [Validators.required]],
      enddate: [enddate, [Validators.required]]
    })
  }


  get skills() {
    return this.createUserForm.get('skills');
  }


  get contact() {
    return this.createUserForm.get('contact');
  }

  get address() {
    return this.createUserForm.get('address');
  }
  get education() {
    return this.createUserForm.get('education');
  }
  get userName() {
    return this.createUserForm.get('userName');
  }
  get workexp() {
    return this.createUserForm.get('workexp');
  }
  get company() {
    return this.createUserForm.get('company');
  }
  get designation() {
    return this.createUserForm.get('designation');
  }
  get companyexp() {
    return this.createUserForm.get('companyexp');
  }
  get jobdes() {
    return this.createUserForm.get('jobdes');
  }

  get enddate() {
    return this.createUserForm.get('enddate');
  }
  get startdate() {
    return this.createUserForm.get('startdate');
  }




  addItem() {
    this.arr = this.createUserForm.get('arr') as FormArray;
    this.arr.push(this.createItem());
  }

  createUser(createUserForm) {
    let employee: any = {
      "id": 0,
      "userName": createUserForm.userName,
      "contact": createUserForm.contact,
      "address": createUserForm.address,
      "education": createUserForm.education,
      "skills": createUserForm.skills,
      "workexp": createUserForm.workexp,
      "workList": createUserForm.arr
    }
    this._service.createUser(employee).subscribe((response) => {
      this.responsecreate = response
      this.newUserAdded = true;
      this.createErrorMessage = null;

    }, (error: HttpErrorResponse) => {
      this.createErrorMessage = error.message;
      if (error instanceof Error) {
      } else {
      }
    })
  }


  register() {
    this.createenable = true;
    this.allemployees = false;
  }

  getallemp() {
    this.ngOnInit();
  }

  Delete(id: any) {
    this._service.delete(id).subscribe((response) => {

      this.deleted = true
      this.createErrorMessage = null;
      this.getallemp();

    }, (error: HttpErrorResponse) => {
      this.createErrorMessage = error.message;
      if (error instanceof Error) {
      } else {
      }
    })
  }

  next(createUserForm) {

    if (createUserForm.userName == '' || createUserForm.contact == '' || createUserForm.address == '' || createUserForm.education == '' || createUserForm.skills == '') {

      this.nextenable = false;
      this.workdetails = false;
    }
    else {
      this.nextenable = true;
      this.workdetails = true;
    }


  }
}
